body {
  overflow-x: hidden; }

.entry-content .alignwide {
  margin-left: -80px;
  margin-right: -80px; }

.entry-content .alignfull {
  margin-left: calc( -100vw / 2 + 100% / 2);
  margin-right: calc( -100vw / 2 + 100% / 2);
  max-width: 100vw;
  width: auto; }

.alignfull img {
  width: auto;
  -o-object-fit: contain;
     object-fit: contain; }

.screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0; }
